import React, { useState } from "react";
import { Modal, Button, Form, Input } from "antd";
import { EditOutlined } from "@ant-design/icons";

const deleteClassModal = () => {
    const [modal, showModal] = useState(false);
    showDeleteConfirm = () => {
        const { nameData } = this.props;
        confirm({
            title: `Are you sure delete ${nameData} class?`,
            icon: <ExclamationCircleOutlined />,
            content:
                "If this class is deleted, then the data associated with this class will be lost.",
            okText: "Yes",
            okType: "danger",
            cancelText: "No",
            onOk() {
                console.log("OK");
            },
            onCancel() {
                console.log("Cancel");
            }
        });
    };
    return (
        <Button type="danger" onClick={this.showDeleteConfirm}>
            <DeleteOutlined />
        </Button>
    );
};

export default deleteClassModal;
